import React,{useEffect,useState} from 'react';


import { ThemeProvider } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Rating from '@material-ui/lab/Rating';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import Divider from '@material-ui/core/Divider';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import IconButton from '@material-ui/core/IconButton';
import GetAppIcon from '@material-ui/icons/GetApp';
import {Paper,CircularProgress} from '@material-ui/core';
import axios from "axios";
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import { observer, inject} from 'mobx-react';

import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import { Link } from "react-router-dom";
import { Routes } from '../../../Routes'

const useStyles = makeStyles(theme => ({
    item:{
        height: 110,
    },
    itemImg:{
        height: 100,
        marginRight:theme.spacing(2)
    },
    itemText:{
      display: 'flex',
      justifyContent: 'center',
      flexDirection: 'column',
      height:100
    }
}));





const UserMangaList = (props) => {
  const userId = props.match.params.userId
  const classes = useStyles();
  const [isLoading,setIsLoading]=useState(true)
  const [list,setList]=useState([])
  const fetchSubscriptions = async userId => {
    setIsLoading(true)
    console.log('fetchSub', userId)
    try {
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/manga/bookmark/${userId}`)
      console.log('üëâ Returned bookmark h data:', response.data);
      setList(response.data.list)
      setIsLoading(false)
    } catch (e) {
      console.log(`üò± Axios request failed: ${e}`);
    }   
  }
  useEffect(()=>{
    fetchSubscriptions(userId)
  },[])


  if(isLoading){
    return <div style={{paddingLeft:'calc(50% - 10px)',paddingTop:40}}><CircularProgress color="secondary" /></div>
  }
  return(
    <div>
      <List className={classes.root}>
{
    list.map(bookmark=>(
        <ListItem alignItems="flex-start" className={classes.item}>
        <ListItemAvatar>
          <Link to={Routes.Manga.link(bookmark.mangaId)}>
            <img src={bookmark.manga.imgUrl} className={classes.itemImg}></img>
          </Link>
        </ListItemAvatar>
        <ListItemText
        className={classes.itemText}
        primary={<Typography color="textPrimary" component={Link} to={Routes.Manga.link(bookmark.mangaId)}>{bookmark.manga.title}<Typography component="span" color='textSecondary'> / {bookmark.manga.englishTitle}</Typography></Typography>}
        secondary={<Link to={Routes.ReadManga.link(bookmark.mangaId, bookmark.chapterId)}>–ü–æ—Å–ª–µ–¥–Ω—è—è –ø—Ä–æ—á–∏—Ç–∞–Ω–Ω–∞—è –≥–ª–∞–≤–∞ {bookmark.chapter.volume} - {bookmark.chapter.chapter}</Link>}
        />
        <ListItemSecondaryAction>
         <Typography variant="body2" color='textSecondary'>
            –ø—Ä–æ—á–∏—Ç–∞–Ω–æ
          </Typography>
          <Typography color="textPrimary" align='center' variant="body2">
            {(new Date(bookmark.createdAt)).toLocaleDateString({day : 'numeric', month : 'numeric', year : 'numeric'}).split('/').join('.')}
          </Typography>
        </ListItemSecondaryAction>
        </ListItem>
    ))
}
      </List>
    </div>
  )}


  export default UserMangaList;